#header{
  width: 100%;
  height: 70vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 40px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: #3498db;
  font-size: 40px;
}
#header h2{
  color: #484848;
  font-size: 22px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 40px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}
.header-img{
  text-align:right ;
 
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
transform: translateY(10px);
  }
  100%{
transform: translateY(-10px);
  }
}

@keyframes up-down{
  0%{
transform: translateY(10px);
  }
  100%{
transform: translateY(-10px);
  }
}
